import React, { useState } from "react"
import * as firebase from 'firebase/app';
import 'firebase/database';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSpinner } from '@fortawesome/free-solid-svg-icons';

const config = {
    apiKey: "AIzaSyBxgrhYdpPp8tY53oWf53evLPrOkREFQV0",
    authDomain: "odessajs-2017.firebaseapp.com",
    databaseURL: "https://odessajs-2017.firebaseio.com",
    projectId: "odessajs-2017",
    storageBucket: "odessajs-2017.appspot.com",
    messagingSenderId: "72618231509"
};
const firebaseApp = firebase.default.initializeApp(config);

const validateEmail = (email) => {
    var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
};

export default ({ data }) => {
    const [showSpiner, setShowSpiner] = useState(false);

    const subscribeEmail = (email, name) => {
        const id = Date.now();
        return firebaseApp.database().ref('emails/' + id).set({email, name});
    };
    const onSubmit = (event) => {
        event.preventDefault();
        setShowSpiner(true);
        const name = event.target[0].value;
        const email = event.target[1].value;
        const target = event.target;
        if (validateEmail(email)) {
            subscribeEmail(email, name).then(function() {
                setShowSpiner(false);
                target[0].value = '';
                target[1].value = '';
                alert('Благодарим за подписку.');
            });
        } else {
            alert('Невалидный e-mail!');
            setShowSpiner(false);
        }
    };
    return (
        <footer id="footer">
            <div className="container my-auto">
                <div className="row">
                    <div className="col-lg-12 mx-auto">
                        <h2 className="text-uppercase main_title text-center">
                            <strong>Keep in touch</strong>
                        </h2>
                        <hr/>
                    </div>
                    <div className="col-lg-4 mx-auto text-center">
                        <p className="caption">
                            Follow us on social media for more information and news about the
                            conference. See you soon!
                        </p>

                        <div className="organizers__list">
                            <div className="organizers__list-item">
                                <div className="organizers__list-item-info">
                                    Email:
                                    <a href="mailto:odessajsconf@gmail.com"><strong>odessajsconf@gmail.com</strong></a>
                                    <br/>
                                    tel:
                                    <a href="tel:+380636116011"><strong>+380 (63) 611 60 11</strong></a>
                                    <br/>
                                    <br/>
                                    <br/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-lg-12 ml-auto text-center">
                        <div className="socials">
                            <form className="subscribe_form" onSubmit={onSubmit}>
                                <div className="form-group row">
                                    <div className="col-12 col-sm-9 col-md-7 col-lg-3 mx-auto">
                                        <input type="text" name="ojs_name" id="ojs_name2" placeholder="your name"
                                               className="form-control subscribe-name-input"/>
                                    </div>
                                </div>
                                <div className="form-group row">
                                    <div className="col-12 col-sm-9 col-md-7 col-lg-3 mx-auto">
                                        <input type="email" name="ojs_email" id="ojs_email2" placeholder="email address"
                                               className="form-control subscribe-email-input"/>
                                    </div>
                                </div>
                                <div className="form-group row">
                                    <div className="col-12 col-sm-9 col-md-7 col-lg-2 mx-auto">
                                        <button type="submit" className="btn btn-primary subscribe-button">
                                            <span style={{ display: showSpiner ? 'none' : 'block' }}>SUBSCRIBE</span>
                                            <FontAwesomeIcon icon={faSpinner} className="fa fa-spin" style={{ display: showSpiner ? 'block' : 'none' }}/>
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>


                <div className="row" id="archive">
                    <div className="col-sm-12 text-center">
                        <div className="footer__last">
                            <a href="http://old.odessajs.org/2013/index.html">OdessaJS 2013</a>
                            <a href="http://old.odessajs.org/2014/index.html">OdessaJS 2014</a>
                            <a href="http://old.odessajs.org/2015/index.html">OdessaJS 2015</a>
                            <a href="http://old.odessajs.org/2016/index.html">OdessaJS 2016</a>
                            <a href="http://old.odessajs.org/2017/index.html">OdessaJS 2017</a>
                            <a href="http://old.odessajs.org/2018/index.html">OdessaJS 2018</a>
                            <a href="http://old.odessajs.org/2019/index.html">OdessaJS 2019</a>
                            <a href="http://old.odessajs.org/2020/index.html">OdessaJS 2020</a>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    );
}

